<!-- 订单进度搜索页面 -->
<template>
  <div class="fenix-container">
    <!-- 搜索框 -->
    <div
      :class='{"fenix-toggle": hasGetResult}'
      class="fenix-searcher">
      <fenix-searcher></fenix-searcher>
    </div>
    <!-- 结果 -->
    <div
      v-show='hasGetResult'
      class="fenix-result">
      <fenix-result></fenix-result>
    </div>
  </div>
</template>
<script>
  import Searcher from './search/Searcher';
  import Result from './search/Result';

  export default {
    components: {
      "fenix-searcher": Searcher,
      "fenix-result": Result
    },
    computed: {
      hasGetResult() {
        return this.$store.getters.scheduleOrderInfos.length > 0;
      }
    }
  }
</script>


<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .fenix-container {
    min-width: 1000px;
    margin: 0 10px;
    /*overflow: hidden;*/
  }

  .fenix-searcher {
    height: 400px;
    padding-top: 200px;
    transition: padding 1s, height 1s;
  }

  .fenix-searcher.fenix-toggle {
    height: 60px;
    padding-top: 10px;
  }
</style>



