<!-- 单个订单进度详情（搜索结果） -->
<template>
  <div class="fenix-schedule-result">
    <!-- 横向概览进度条 -->
    <el-steps
      :active='activeIndex'
      finish-status="success"
      simple>
      <el-step
        v-for='(name, index) in overviewSteps'
        :key='index'
        :title= 'name'>
      </el-step>
    </el-steps>
    <!-- 订单信息概览以及进度详细信息 -->
    <el-collapse
      @change='collapseChange'
      v-model='activeCollapse'>
      <!-- 订单信息概览 -->
      <el-collapse-item name="1">
        <template :class='"fenix-header"' slot="title">
          <i class="el-icon-view"></i>
          <span class="fenix-title">
            订单信息概览
          </span>
        </template>
        <!-- 订单信息概览 -->
        <!-- 点击了该面板才加载 -->
        <fenix-order-overview
          v-if='getOrderInfo'
          :orderId='orderId'>
        </fenix-order-overview>
      </el-collapse-item>
      <!-- 进度详细信息 -->
      <el-collapse-item class="fenix-schedule-info" name="2">
        <template slot="title">
          <i class="el-icon-tickets"></i>
          <span class="fenix-title">
            订单进度详情
          </span>
        </template>
        <!-- 订单进度详情 -->
        <fenix-schedule-detail
          @setActiveStep='setActiveStep'
          :orderId='orderId'>
        </fenix-schedule-detail>
      </el-collapse-item>
    </el-collapse>
  </div>
</template>

<script>
  import OrderOverview from '@/components/parts/OrderOverview';
  import ScheduleDetail from './ScheduleDetail';

  export default {
    components: {
      "fenix-order-overview": OrderOverview,
      "fenix-schedule-detail": ScheduleDetail
    },
    data() {
      return {
        // 进度条数据
        activeIndex: -1, // 当前激活的节点
        overviewSteps: [ // 进度条基础数据，固定五个节点
          '回交期',
          '领料',
          '排计划',
          '生产',
          '交货'
        ],
        activeCollapse: ['2'], // 激活的面板name集合
        getOrderInfo: false // 是否加载对应订单信息
      }
    },
    props: [
      'orderId'
    ],
    methods: {
      // 折叠面板状态改变处理函数
      // 回调参数：(activeNames: array|string)
      // 如果打开的是订单详情折叠面板，
      // 则将订单详情插入页面（减少不必要的渲染）
      collapseChange(activeNames) {
        if (!this.getOrderInfo) {
          this.getOrderInfo = activeNames.some(item => item === '1');
        }
      },
      // 设置当前激活的进度条
      setActiveStep(activeIndex) {
        this.activeIndex = activeIndex;
      }
    }
  }
</script>


<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  /* 订单进度详情步骤条样式 */
  .el-step {
    max-width: 100%;
  }
  .el-steps li{
    font-size: 14px;
    color: #2D2F33;
  }

  /* 设置成两栏布局 */
  .dateTime {
    position: absolute;
    padding-left: 10px;
    width: 200px;
  }
  .infoText {
    margin-left: 210px;
  }
  /* 分别对四个span进行样式微调 */
  .fenix-date, .fenix-week{
    margin-right: 10px;
    font-weight: 600;
  }
  .fenix-time {
    font-style: italic;
    margin-right: 20px;
  }
  .fenix-text {
    color: #878D99;
  }

  /* 透明度设为零 */
  .fenix-hidden {
    opacity: 0;
  }
</style>



