<!-- 订单进度详情搜索框 -->
<template>
  <div class="fenix-detail-info">
    <div class="fenix-overview-text">
      <p>
        当前生产状态为S1，订单数量1000PCS，已完成500PCS，预计本工序完成时间2017.11.14，预计订单完成时间2017.11.17
        请耐心等待...
      </p>
    </div>
    <el-steps
      direction="vertical"
      finish-status="success"
      :active="1">
      <el-step title="回交期">
        <ul slot="description">
          <li>
            <div class="dateTime">
              <span class="fenix-date">2017-11-12</span>
              <span class="fenix-week">周日</span>
              <span class="fenix-time">11:13:54</span>
            </div>
            <div class="infoText">
              <span class="fenix-text">【计划组】【李涛】向【采购部】【全海燕】确认交期</span>
            </div>
          </li>
        </ul>
      </el-step>
      <el-step title="领料">
        <ul slot="description">
          <li>
            <div class="dateTime">
              <span class="fenix-date">2017-11-12</span>
              <span class="fenix-week">周日</span>
              <span class="fenix-time">16:13:54</span>
            </div>
            <div class="infoText">
              <span class="fenix-text">【物资保障组】【陈铁牛】领取材料112支</span>
            </div>
          </li>
        </ul>
      </el-step>
      <el-step title="排计划">
        <ul slot="description">
          <li>
            <div class="dateTime">
              <span class="fenix-date">2017-11-12</span>
              <span class="fenix-week">周日</span>
              <span class="fenix-time">11:13:54</span>
            </div>
            <div class="infoText">
              <span class="fenix-text">【计划组】【李涛】处理订单并安排生产计划</span>
            </div>
          </li>
        </ul>
      </el-step>
      <el-step title="生产-S1">
        <ul slot="description">
          <li>
            <div class="dateTime">
              <span class="fenix-date">2017-11-12</span>
              <span class="fenix-week">周日</span>
              <span class="fenix-time">19:13:54</span>
            </div>
            <div class="infoText">
              <span class="fenix-text">【筒头组】【胡小涛】开始调机</span>
            </div>
          </li>
          <li>
            <div class="dateTime">
              <span class="fenix-date fenix-hidden">2017-11-12</span>
              <span class="fenix-week fenix-hidden">周日</span>
              <span class="fenix-time">22:13:54</span>
            </div>
            <div class="infoText">
              <span class="fenix-text">【筒头组】【胡小涛】开始生产</span>
            </div>
          </li>
          <li>
            <div class="dateTime">
              <span class="fenix-date">2017-11-13</span>
              <span class="fenix-week">周一</span>
              <span class="fenix-time">08:13:54</span>
            </div>
            <div class="infoText">
              <span class="fenix-text">【筒头组】【胡小涛】生产200PCS，其中良品198PCS，报废2PCS</span>
            </div>
          </li>
        </ul>
      </el-step>
      <el-step title="交货">
        <ul slot="description">
          <li>
            <div class="dateTime">
              <span class="fenix-date">2017-11-17</span>
              <span class="fenix-week">周五</span>
              <span class="fenix-time">11:13:54</span>
            </div>
            <div class="infoText">
              <span class="fenix-text">【物料保障组】【陈铁牛】发往【反光杯组】1000PCS</span>
            </div>
          </li>
        </ul>
      </el-step>
      <el-step
        title="完成状态">
        <h2 slot="description">
          已完成
        </h2>
      </el-step>
    </el-steps>
  </div>
</template>

<script>

  export default {
    components: {
    },
    data() {
      return {
        sourceData: {}
      }
    },
    props: [
      'orderId'
    ],
    methods: {
      // 获取页面所需数据
      getInfos() {

      }
    },
    mounted() {
      this.$nextTick(function() {
        this.$emit('setActiveStep', 1);
      });
    }
  }
</script>


<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  /* 订单进度详情步骤条样式 */
  .el-step {
    max-width: 100%;
  }
  .el-steps li{
    font-size: 14px;
    color: #2D2F33;
  }

  /* 设置成两栏布局 */
  .dateTime {
    position: absolute;
    padding-left: 10px;
    width: 200px;
  }
  .infoText {
    margin-left: 210px;
  }
  /* 分别对四个span进行样式微调 */
  .fenix-date, .fenix-week{
    margin-right: 10px;
    font-weight: 600;
  }
  .fenix-time {
    font-style: italic;
    margin-right: 20px;
  }
  .fenix-text {
    color: #878D99;
  }

  /* 透明度设为零 */
  .fenix-hidden {
    opacity: 0;
  }
</style>



