import axios from 'axios';

// 获取订单信息
// data：被formData序列化的文件主体
// params：随文件发送的参数（订单信息的id）
export function getOrderInfos(params) {
  return axios({
    url: '../../order/orderDetail.do',
    method: 'get',
    params: params
  });
};




