<!-- 订单进度搜索页面 -->
<template>
  <div class="fenix-container">
    <el-row :gutter='10'>
      <el-col :span='10' :offset='1'>
        <span>订单号：</span>
        <!-- 做了一定封装的带有输入建议的组件 -->
        <fenix-autocomplete
          v-model="orderNo"
          placeholder="请输入订单号..."
          suggestName="orderNo">
        </fenix-autocomplete>
      </el-col>
      <el-col :span='11'>
        <span>产品名称：</span>
        <fenix-autocomplete
          v-model="productName"
          placeholder="请输入产品名称..."
          suggestName="productName">
        </fenix-autocomplete>
        <!-- 搜索按钮图标 -->
        <i @click='doSearch'
          class="fenix-search el-icon-search">
        </i>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  import FenixAutocomplete from '@/components/parts/FenixAutocomplete';

  export default {
      components: {
        FenixAutocomplete
      },
      data() {
        return {
          orderNo: '',
          productName: ''
        }
      },
      methods: {
        doSearch() {
          // 简单过滤不必要的请求
          if (this.orderNo || this.productName) {
            const params = {
              orderNo: this.orderNo,
              productName: this.productName
            }

            // 发起获取数据请求
            this.$store.dispatch('schedule/getOrderInfos', params);
          } else {
            this.$notify({
              type: 'warning',
              message: '必须至少输入订单号或产品名称...'
            });
          }
        }
      }
    }
</script>


<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .el-row {
    /*height: 30px;*/
  }
  /* 左侧输入框文本靠右对齐 */
  .el-col:nth-of-type(1) {
    text-align: right;
  }
  span {
    font-weight: 600;
    color: #5A5E66;
    font-size: 18px;
  }
  .fenix-search {
    margin-left: 20px;
    font-size: 26px;
    vertical-align: middle;
    transition: transform 0.5s;
  }
  .fenix-search:hover {
    color: #409EFF;
    /*font-size: 30px;*/
    font-weight: 900;
    transform: rotate(90deg);
    cursor: pointer;
  }

  .el-input {
    width: 300px;
  }
</style>



