<template>
  <el-autocomplete
    v-model="inputVal"
    spellcheck="false"
    :fetch-suggestions="querySearch"
    :placeholder='placeholder'
    @select="handleSelect">
    <i class="el-icon-circle-close fenix-clear-input"
      slot="suffix"
      @click="clearInput">
    </i>
    <template slot-scope="props">
      <div class="name">{{ props.item.value }}</div>
    </template>
  </el-autocomplete>
</template>

<script>
  export default {
    data() {
      return {
        // 输入框绑定值
        inputVal: ''
      }
    },
    props: {
      suggestName: '',
      placeholder: ''
    },
    // 输入框建议值
    computed: {
      inputArr() {
        return this.$store.getters.suggestionGetSuggestions(this.suggestName);
      }
    },
    watch: {
      // 监视输入框的变动
      // 如果有变化则触发父组件的input事件
      // 在此处还能对输入值进行验证以及过滤
      inputVal: {
        handler(val, oldVa) {
          this.$emit('input', val);
        },
        immediate: true
      }
    },
    methods: {
      // 建议备选项的过滤处理程序
      // cb：回调函数，用来返回过滤后的结果
      querySearch(queryString, cb) {
        // 如果建议值不存在，则发起获取数据操作
        if (!this.inputArr) {
          this.getSuggestions();
        }

        const inputArr = this.inputArr;

        const results = queryString ?
                        inputArr.filter(this.createStateFilter(queryString)) :
                        inputArr;

        // 调用callback返回建议列表的数据
        cb(results);
      },
      // 建议备选项过滤使用的过滤函数
      // 不区分大小写
      // 支持由中间部分开始匹配
      createStateFilter(queryString) {
        return state => {
          return (state.value.toLowerCase().indexOf(queryString.toLowerCase()) >= 0);
        }
      },
      // 点击图标清空输入框
      clearInput() {
        this.inputVal = '';
      },
      // 根据父组件传入的suggestName
      // 获取对应的建议值
      getSuggestions() {
        this.$store.dispatch('suggestions/getSuggestions', this.suggestName)
      },
      // 建议列表单击事件处理程序
      handleSelect(item) {

      }
    },
    // $el挂载完毕后请求建议值
    mounted() {
      this.$nextTick(function() {
        this.getSuggestions();
      });
    }
  }
</script>

<style scoped>
  /* 行高40px实现垂直居中 */
  .fenix-clear-input {
    text-align: center;
    line-height: 40px;
    width: 25px;
    font-size: 20px;
    color: #E6EBF5;
  }
  .fenix-clear-input:hover {
    color: #FA5555;
    cursor: pointer;
  }
</style>



