<!-- 订单进度搜索结果部分 -->
<template>
  <div class="fenix-container">
    <!-- 使用手风琴展示多个搜索结果 -->
    <el-collapse
      v-model="activeName"
      @change='collapseChange'
      accordion>
      <el-collapse-item
        v-for="(order, index) in showInfos"
        :key='index'
        :name='index'>
        <template slot="title">
          <el-row class="fenix-collapse-header">
            <el-col :span='4'>
              <span>订单号：</span><span>{{order.orderNo}}</span>
            </el-col>
            <el-col :span='4'>
              <span>产品名称：</span><span>{{order.productName}}</span>
            </el-col>
            <el-col :span='3'>
              <span>订单数量：</span><span>{{order.orderAmount}}</span>
            </el-col>
            <el-col :span='13'>
              <span>特别要求：</span><span>{{order.id}}</span>
            </el-col>
          </el-row>
        </template>
        <!-- 传入订单号的id用于确认唯一的一条订单 -->
        <!-- 如果name和activeName相等，则发起加载详细信息请求 -->
        <fenix-result-item
          v-if='insertCollapses[activeName]'
          :orderId='order.id'>
        </fenix-result-item>
        <div v-else class="fenix-on-loading">
          <i class="el-icon-loading"></i>
          <span>详细信息加载中，请稍后...</span>
        </div>
      </el-collapse-item>
    </el-collapse>
    <div class="fenix-loadmore">
      <el-button
        @click='loadMore'
        v-show='showButton'
        :loading='loading'
        type="text">
      {{loading ? "加载中..." : "点击加载更多"}}
    </el-button>
    </div>
  </div>
</template>
<script>
  import FenixResultItem from './ResultItem';

  export default {
    components: {
      FenixResultItem
    },
    data() {
      return {
        activeName: -1, // 默认打开的折叠面板
        showNumber: 10, // 展示的结果条数
        loading: false,
        insertCollapses: {} // 插入折叠面板的内容，实现懒加载
      }
    },
    computed: {
      resultInfos() {
        return this.$store.getters.scheduleOrderInfos;
      },
      // 如果数据过多，则进行截取
      // 并通过用户单击按钮加载更多
      showInfos() {
        const showNumber = this.showNumber < this.resultInfos.length ?
          this.showNumber : this.resultInfos.length;

        // slice第二个参数是数组结束处的下标
        // 如果该参数为数组长度，则超出下标范围，此时截取整个数组
        return this.resultInfos.slice(0, showNumber);
      },
      // 如果展示的数量比结果数量少，则显示出“加载更多”按钮
      showButton() {
        return this.showNumber < this.resultInfos.length;
      }
    },
    watch: {
      showInfos: {
        handler: function(val, oldVal) {
          // 如果只有一条结果，则默认打开该折叠面板
          // 然后手动调用一下点击折叠面板处理函数
          if (val.length === 1) {
            this.activeName = 0;
            this.collapseChange(this.activeName);
          }
          // 如果showInfos发生了变化，则说明数据加载已经结束
          this.loading = false;
        },
        deep: true,
        immediate: true
      }
    },
    methods: {
      // 加载更多数据
      loadMore() {
        this.loading = true;
        this.showNumber += 10;
      },
      // 点击折叠面板处理函数
      // 设置对应面板内容加载标识为true
      collapseChange(activeName) {
        if (!this.insertCollapses[activeName]) {
          this.$set(this.insertCollapses, activeName, true);
        }
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  /* 折叠面板头部样式 */
  .fenix-collapse-header {
    overflow: auto;
    height: 48px;
  }
  .el-collapse-item span:nth-of-type(2n + 1) {
    font-weight: 600;
  }
  .el-collapse-item span:nth-of-type(2n) {
    margin-right: 20px;
    color: #878D99;
  }

  /* 结果详情加载中界面 */
  .fenix-on-loading {
    line-height: 60px;
    text-align: center;
    vertical-align: middle;
  }
  .fenix-on-loading i {
    font-size: 60px;

  }
  /* 更多结果加载中按钮 */
  .fenix-loadmore {
    text-align: center;
  }

</style>



