<template>
  <!-- 订单信息概览 -->
  <div class="fenix-order-info">
    <el-row>
      <el-col :xs='24' :sm='12' :lg='6'>
        <span>订单号：</span>
        <span>{{orderInfo.orderNo}}</span>
      </el-col>
      <el-col :xs='24' :sm='12' :lg='6'>
        <span>产品名称：</span>
        <span>{{orderInfo.productName}}</span>
      </el-col>
      <el-col :xs='24' :sm='12' :lg='6'>
        <span>订单类型：</span>
        <span>{{orderInfo.orderType}}</span>
      </el-col>
      <el-col :xs='24' :sm='12' :lg='6'>
        <span>单价：</span>
        <span>{{orderInfo.price}}</span>
      </el-col>
    </el-row>
    <el-row>
      <el-col :xs='24' :sm='12' :lg='6'>
        <span>下单日期：</span>
        <span>{{orderInfo.orderDate}}</span>
      </el-col>
      <el-col :xs='24' :sm='12' :lg='6'>
        <span>要求交货日期：</span>
        <span>{{orderInfo.requiredDate}}</span>
      </el-col>
      <el-col :xs='24' :sm='12' :lg='6'>
        <span>交期：</span>
        <span>{{orderInfo.replyDate}}</span>
      </el-col>
      <el-col :xs='24' :sm='12' :lg='6'>
        <span>生产状态：</span>
        <span>{{orderInfo.orderStatus}}</span>
      </el-col>
    </el-row>
    <el-row>
      <el-col :xs='24' :sm='12' :lg='6'>
        <span>订单数量：</span>
        <span>{{orderInfo.orderAmount}}</span>
      </el-col>
      <el-col :xs='24' :sm='12' :lg='6'>
        <span>库存数量：</span>
        <span>{{orderInfo.stockAmount}}</span>
      </el-col>
      <el-col :xs='24' :sm='12' :lg='6'>
        <span>备品数量：</span>
        <span>{{orderInfo.spareParts}}</span>
      </el-col>
      <el-col :xs='24' :sm='12' :lg='6'>
        <span>需生产数量：</span>
        <span>{{orderInfo.planAmount}}</span>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="24">
        <span>特别要求：</span>
        <span>{{orderInfo.purchaseRequirement}}</span>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="24">
        <span>规格型号：</span>
        <span>{{orderInfo.materialModel}}</span>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="24">
        <span>物料长代码：</span>
        <span>{{orderInfo.materialCoding}}</span>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="24">
        <span>异常原因：</span>
        <span>{{orderInfo.exceptionReason}}</span>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="24">
        <span>备注：</span>
        <span>{{orderInfo.remark}}</span>
      </el-col>
    </el-row>
  </div>
</template>
<script>
  import { getOrderInfos } from '@/api/orderInfo'
  export default {
    data() {
      return {
        orderInfo: {}
      }
    },
    props: {
      orderId: ''
    },
    watch: {
      orderId: {
        handler: function(val, oldVal) {
          this.getData();
        }
      }
    },
    methods: {
      getData() {
        const params = {
          id: this.orderId
        };

        // 发起获取数据请求
        getOrderInfos(params).then((res) => {
          const order = res.data.order;

          this.orderInfo = order;
          this.$message({
            type: 'success',
            message: '数据更新成功'
          })
        }, (err) => {
          this.notify({
            type: 'error',
            message: '数据获取失败，请重试或者联系管理员。。。'
          })
        });
      }
    },
    mounted() {
      this.$nextTick(function() {
        this.getData();
      });
    }
  }
</script>


<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  /* 订单概览使用表格 */
  .el-row {
    /*min-height: 50px;*/
    /*overflow: auto;*/
    line-height: 50px;
    padding: 0 10px;
    font-size: 18px;
    /*min-width: 1000px;*/
  }
  /* 斑马条纹 */
  .el-row:nth-of-type(2n) {
    background-color: #EDF2FC;
  }
  .el-row:nth-of-type(2n + 1) {
    background-color: #E6EBF5;
  }
  .el-row span:nth-of-type(1) {
    font-weight: 500;
    color: #5A5E66;
  }
  .el-row span:nth-of-type(2) {
    font-size: 16px;
    color: #878D99;
  }
</style>



